import React, { useEffect, useState } from "react";
import SurveyQues from "./SuveyQues/SurveyQues";
import "./SurveyPage.scss";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { postSurveyQs } from "../../redux/reducer/DailyQsReducer";
import AstronautSpinner from "../animations/Spinner/AstronautSpinner";
import { Navigate, useNavigate } from "react-router-dom";
import TestMoon from "../Detail/TestMoon";

export default function Question1() {
  const [toggle, setToggle] = useState(false);
  const getColor = useSelector((state) => state.toggle.user.color);
  if (getColor) {
    return <Navigate to="../" />;
  }
  return (
    <div
      style={{
        width: "100vw",
        height: "100vh",
        backgroundColor: "#363636",
      }}
    >
      <div className="surveySp">
        <div
        // style={{ overflowY: "auto" }}
        >
          {toggle ? <HereResult /> : <SurveyQues setToggle={setToggle} />}
        </div>
      </div>
    </div>
  );
}

export function HereResult() {
  const result = useSelector((state) => state.dailyQuestions.daily.answer);
  const loading = useSelector((state) => state.dailyQuestions.qsLoading);
  const getColor = useSelector((state) => state.dailyQuestions.color);
  const nickname = useSelector((state) => state.toggle.user.nickname);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  console.log(getColor);
  useEffect(() => {
    dispatch(postSurveyQs(result));
  }, []);
  return (
    <div>
      {loading && !getColor ? (
        <AstronautSpinner
          setStyle={{
            width: "100%",
            height: "100%",
            backgroundColor: "transparent",
          }}
        />
      ) : (
        <section className="survey__section">
          <div className="survey__result">
            <h1>오늘의 색깔</h1>
            <div className="survey__comments">
              <div className="survey__comments__wrapper">
                <h1 className="nick">{nickname} 님!</h1>
                {getColor && (
                  <div className="comments">
                    <h1>{getColor.head}</h1>
                    <h1 style={{ color: getColor.color }}>{getColor.body}</h1>
                    <h1 style={{ whiteSpace: "nowrap" }}>{getColor.footer}</h1>
                  </div>
                )}

                <button onClick={() => navigate("../calendar")}>
                  캘린더에서 보기
                </button>
                <button
                  onClick={() => {
                    navigate("../../detail");
                  }}
                >
                  HOME
                </button>
              </div>
            </div>
          </div>
          <div
            style={{
              width: "200%",
              height: "100%",
              top: "65%",
              left: "55%",
              transform: "translate(-50%,-50%)",
              zIndex: "-1",
              position: "absolute",
            }}
          >
            <TestMoon
              currentColor={getColor?.color ?? "green"}
              style={{ height: "100vh" }}
            />
          </div>
        </section>
      )}
    </div>
  );
}
